import React from "react";
import { connect } from "react-redux";
import VKConnect from "@vkontakte/vkui-connect-promise";
import { Panel, Group, Avatar, PanelHeader, CellButton, Div, FormStatus, topPlace, List, Switch, Cell, Counter } from "@vkontakte/vkui";
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Icon24Settings from '@vkontakte/icons/dist/24/settings';

const Transaction = ({ id, back, transaction, go }) => {
    let date = new Date();
    date.setTime((transaction.time || 0) * 1000);
    return (
        <Panel id={id}>
            <PanelHeader
                left={<PanelHeaderBack onClick={back} />}
            >
                Настройки
            </PanelHeader>

           <Group title="Дополнительно">
					<List>
						<Cell
							expandable
							before={<Icon24Settings/>}
							indicator={topPlace !== 0 ? <Counter type="primary"><span style={{padding: '0 5px'}}>Ваше место: {topPlace}</span></Counter> : null}
							onClick={() => go("")}
						>
							Темная тема
						</Cell>
                        <Cell
							expandable
							before={<Icon24Settings/>}
							indicator={topPlace !== 0 ? <Counter type="primary"><span style={{padding: '0 5px'}}>Ваше место: {topPlace}</span></Counter> : null}
							onClick={() => go("")}
						>
							Светлая тема
						</Cell>
					</List>
				</Group>
    
        </Panel>
    );
};

const mapProps = (state) => ({
    transaction: state.transactions.selected
});

const mapDispatch = ({ navigator: { goBack } }) => ({
    back: goBack
});

export default connect(mapProps, mapDispatch)(Transaction);